'use client';
import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { usePathname } from 'next/navigation';
import { Menu, X, Phone, Mail } from 'lucide-react';
import { useTheme } from '../contexts/ThemeContext';
import { getImageSrc } from '../utils/imageHelpers';
import lightImage from '../assets/images/logo-light.png';
import darkImage from '../assets/images/logo-dark.png';
import ThemeToggle from './ThemeToggle';
import BookingModal from './BookingModal';

const Header: React.FC = () => {
  const [isScrolled, setIsScrolled] = useState(false);
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const [isBookingModalOpen, setIsBookingModalOpen] = useState(false);
  const { isDarkMode } = useTheme();
  const pathname = usePathname();

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50);
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  const navItems = [
    { name: 'Home', href: '/' },
    { name: 'About', href: '/about' },
    { name: 'Treks', href: '/treks' },
    { name: 'Services', href: '/services' },
    { name: 'Contact', href: '/contact' },
  ];

  const isActive = (href: string) => {
    if (href === '/') return pathname === '/';
    return pathname.startsWith(href);
  };

  return (
    <>
      {/* Top Bar */}
      <div className={`${isDarkMode ? 'bg-gray-800' : 'bg-primary-500'} text-white py-2`}>
        <div className="container mx-auto px-4">
          <div className="flex justify-between items-center text-sm">
            <div className="flex gap-6">
              <a href="tel:+977" className="flex items-center gap-2 hover:text-gray-200 transition-colors">
                <Phone size={14} />
                <span className="hidden sm:inline">+977 98XXXXXXXX</span>
              </a>
              <a href="mailto:info@ngimalaya.com" className="flex items-center gap-2 hover:text-gray-200 transition-colors">
                <Mail size={14} />
                <span className="hidden sm:inline">info@ngimalaya.com</span>
              </a>
            </div>
            <ThemeToggle />
          </div>
        </div>
      </div>

      {/* Main Header */}
      <header className={`sticky top-0 z-50 transition-all duration-300 ${
        isScrolled 
          ? isDarkMode 
            ? 'bg-gray-900/95 backdrop-blur-md shadow-lg' 
            : 'bg-white/95 backdrop-blur-md shadow-lg'
          : isDarkMode 
            ? 'bg-gray-900' 
            : 'bg-white'
      }`}>
        <nav className="container mx-auto px-4">
          <div className="flex items-center justify-between h-20">
            {/* Logo */}
            <Link href="/" className="flex items-center">
              <img
                src={getImageSrc(isDarkMode ? lightImage : darkImage)}
                alt="Ngimalaya Adventure Nepal"
                className="h-12 md:h-14 w-auto"
              />
            </Link>

            {/* Desktop Navigation */}
            <div className="hidden lg:flex items-center space-x-8">
              {navItems.map((item) => (
                <Link
                  key={item.name}
                  href={item.href}
                  className={`font-display font-semibold uppercase tracking-wider text-sm transition-colors duration-300 ${
                    isActive(item.href)
                      ? 'text-primary-500'
                      : isDarkMode
                        ? 'text-gray-300 hover:text-primary-500'
                        : 'text-gray-700 hover:text-primary-500'
                  }`}
                >
                  {item.name}
                </Link>
              ))}
              <button className="bg-primary-500 hover:bg-primary-600 text-white px-6 py-2.5 font-display font-bold uppercase tracking-wider text-sm transition-all duration-300 shadow-lg hover:shadow-xl">
                Book Now
              </button>
            </div>

            {/* Mobile Menu Button */}
            <button
              onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              className={`lg:hidden p-2 ${isDarkMode ? 'text-gray-300' : 'text-gray-700'}`}
            >
              {isMobileMenuOpen ? <X size={24} /> : <Menu size={24} />}
            </button>
          </div>

          {/* Mobile Menu */}
          {isMobileMenuOpen && (
            <div className={`lg:hidden py-4 border-t ${
              isDarkMode ? 'border-gray-700 bg-gray-900' : 'border-gray-200 bg-white'
            }`}>
              {navItems.map((item) => (
                <Link
                  key={item.name}
                  href={item.href}
                  onClick={() => setIsMobileMenuOpen(false)}
                  className={`block py-3 px-4 font-display font-semibold uppercase tracking-wider text-sm transition-colors ${
                    isActive(item.href)
                      ? 'text-primary-500 bg-primary-50'
                      : isDarkMode
                        ? 'text-gray-300 hover:text-primary-500 hover:bg-gray-800'
                        : 'text-gray-700 hover:text-primary-500 hover:bg-gray-50'
                  }`}
                >
                  {item.name}
                </Link>
              ))}
              <div className="px-4 pt-3">
                <button className="w-full bg-primary-500 hover:bg-primary-600 text-white px-6 py-3 font-display font-bold uppercase tracking-wider text-sm transition-all duration-300 shadow-lg">
                  Book Now
                </button>
              </div>
            </div>
          )}
        </nav>
      </header>
    </>
  );
};

export default Header;
